body{
    background-color: black;
    color: aliceblue;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.main{
    text-align: center;
    display: flex;
    flex-direction: column;
    width: 20rem;
    margin: auto;
    justify-content: space-between;
}

.main label{
    margin: 2rem;
}

.main button{
    width: 5rem;
    margin: auto;
    margin-top: 2rem;
}

.main select{
    margin: 1rem;
    padding: 1rem;
    border-radius: 2rem;
    border: none;
}

.main> select> option{
    margin: 1rem;
    padding: 1rem;
    border-radius: 2rem;
    border: none;
}


.main select:focus{
    
    background-color: antiquewhite;
}

.main button{
    background-color: blue;
    border-radius: 2rem;
    color: aliceblue;
    border: none;
    padding: 1rem;
   

}

.main button:hover{
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem #011270;
}

.box{
    background-color: rgb(128, 128, 255);
    border-radius: 2rem;
    height: 20rem;
}

